
//$Font import 
@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+TC:wght@400;700&display=swap");
// @import url("https://stackpath.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css");


/*-------------------------*/
/* base
---------------------------*/
*,
*:before,
*:after {
	position: relative;
  box-sizing: border-box;
}
html {
  font-size: 62.5%;
}
body {
  min-width: 0;
  font-size: 1.6rem;
  // line-height: 1.7;
  // font-size: 2.6vw;
  // line-height: 4vw;
  // 20px / 30px 
  @media screen and (min-width: $mq768) {
    font-size: 1.8rem;
    line-height: 1.5;
    margin: auto;
    background-image: url('../../assets/images/bg_texture.jpg');
    background-repeat: repeat;
  }
}
::selection { 
  color: white; 
  background-color:$primary;
}
img {
  width: 100%;
  max-width: 100%;
  display: block;
  border: 0;
  height: auto;
  @media screen and (min-width: $mq1200) {
    width: auto;
  }
}
h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}
ul, li {
  margin: 0;
  padding: 0;
  list-style: none;
}
a {
  display: inline-block;
  text-decoration: none;
}
input {
  appearance: none;
  background-color: transparent;
}
input,
select,
textarea {
  outline: none;
  border: none;
  resize: none;
  font-size: initial;
}

