/*-------------------------*/
/* moreActivity
---------------------------*/
.moreActivity{
  // 活動文字
  .moreTxt{
    width: 100%;
    max-width: 100%;
    background-color: $primary;
    padding: 2.5rem 0rem;
    @media screen and (min-width: $mq1200) {
      padding: 4.5rem 0rem;
    }
    &:after{
      content: "";
      display: block;
      width:100%;
      height: 0.2rem;
      background-color: $secondary;
      position: absolute;
      left: 0;
      bottom: 1rem;
    }
    &__title{
      @include fontSize(1.7rem , 1.5);
      color: $secondary;
      @media screen and (min-width: $mq1200) {
        @include fontSize(3rem , 1.5);
      }
    }
    &__detail{
      color: $white;
    }
  }
  // 更多活動
  .moreActivity__link{
    display: block;
  }
  .moreActivity__img{
    border: 0.5rem solid $gray-300;
    transition: all 0.3s;
    margin: auto;
    &:hover{
      border: 0.5rem solid $primary;
    }
  }
}


